import react, { useState } from "react";
import Styled from "styled-components";
import { Timepicker } from 'react-timepicker'
import clock from './clock-icon.svg';
const StyledContainer = Styled.div`
   position:absolute;
   top:50%;
   left:50%;
   transform:translate(-50%,-50%);
//    width:80vw;
   margin:0 auto;
//    height:40vh;
   border-left:1px solid white;
   border-top:1px solid white;
   display:flex;
   justify-content:center;
   align-items:center;
   background: linear-gradient(to right bottom,rgba(255, 255, 255,0.1),rgba(133, 104, 104,.3));
   box-shadow:8px 8px 16px 0 rgba(0,0,0,.2), -8px -8px 16px 0 rgba(255,255,255,.1);
   background: #ECE9E6; 
    background: -webkit-linear-gradient(to right, rgba(255,255,255,.2), rgba(236,233,230,.3));  
    
    background: linear-gradient(to right,  rgba(247,247,254,.1), rgba(236,233,230,.2)); 
    overflow:hidden;
`;
const StyledWrapper = Styled.div`
    display:flex;
    flex-direction:column;
    margin:40px;
`;
const Input = Styled.input`
  font-size: 18px;
  padding: 10px;
  width:auto;
  text-align:center;
  background: papayawhip;
  border: none;
  border-radius: 3px;
  letter-spacing: 5px;
  color:palevioletred;
  ::placeholder {
    color: palevioletred;
  }
  ::-webkit-calendar-picker-indicator {
    background: url(${clock});
  }
  ::-webkit-calendar-picker-indicator:hover {
	transform:scale(1.3);
   }
 
`;

const Label = Styled.label`
    color:#37b9f1;
    font-size :2rem;
    letter-spacing:2px;
    font-weight: bold;
    margin-bottom:5px;
    text-shadow: 4px 3px 0 #7A7A7A;
    text-shadow: #e0e0e0 1px 1px 0;
`

const Button = Styled.button`
    color:white;
    background-color:#37b9f1;
    font-size:1em;
    font-weight: bold;
    margin:1rem;
    padding:16px;
    border-radius:6px;
    border:none;
    //box-shadow:0 0 5px 5px #37b9f1;
`
const StyledTitle = Styled.div`
   width:auto;
   height:auto;
   padding:1rem;
   font-size: 2rem;
   margin-top:2rem;
   letter-spacing:2px;
   color: #FFFFFF;
    text-shadow: 4px 3px 0 #7A7A7A;
    text-shadow: #e0e0e0 1px 1px 0;
    color: #37b9f1;
    background: linear-gradient(to right,  rgba(247,247,254,.06), rgba(236,233,230,.09)); 
`


const Converter = () => {
    const [standardTime, setStandardTime] = useState("");
    const [militaryTime, setMilitaryTime] = useState(null);

    const convertToMilitary = (time) => {
        console.log("event- > " + time);
        setStandardTime(time);
        setMilitaryTime(time);
    }

    return (
        <>

            <StyledTitle><h1>Time Converter</h1></StyledTitle>
            <StyledContainer>
                <StyledWrapper>
                    <Label for="">Standard Time</Label>
                    <Input type="time" name="time" id="" placeholder="HH:MM AM/PM" value={standardTime} onChange={e => { convertToMilitary(e.target.value) }} />
                </StyledWrapper>
                <StyledWrapper>
                    <Label for="">Military Time</Label>
                    <Input disabled="disabled" type="text" name="time" id="" placeholder="HH:MM" value={militaryTime} />
                </StyledWrapper>
            </StyledContainer>
        </>
    );
}

export default Converter;
